// Import css
import './scss/index.scss';

if (typeof window.Steenify === 'undefined') {
  window.Steenify = {};
}
// Import js
import './js/home';
import './js/event-details';
import './js/datepicker';
import './js/upload-file';
import './js/flea-market';
import './js/ckEditor';
import './js/timetable';
import './js/lecture-evaluation';

// common js
const clickOrTouch = 'ontouchend' in window ? 'touchend' : 'click';
let lastScroll = 0;

window.Steenify = $.extend(Steenify, {
  init: function() {
    Steenify.init__header();
    Steenify.init__page();
  },
  init__page: function() {
    const view = $('.ewhaian-page').data('view');
    switch (view) {
      case 'home':
        Steenify.init__home();
        break;
      case 'event-details':
        Steenify.init__ed();
        break;
      case 'advertisement':
        Steenify.init__datepicker();
        break;
      case 'advertisement-enrollment':
        Steenify.init__upload__file();
        Steenify.init__CKEditor();
        break;
      case 'flea-market':
        Steenify.init__datepicker();
        Steenify.init__upload__image();
        break;
      case 'event-comments':
        Steenify.init__event_comments();
        break;
      case 'timetable':
        Steenify.init__render__timetable();
        break;
      case 'lecture-evaluation':
        Steenify.init__lecture_evaluation();
        break;
      default:
        console.log(view);
    }
  },
  init__header: function() {
    Steenify.init__datepicker();

    $('.hamburger').on('click', function() {
      const self = $(this);
      const header = $('#header');

      self.toggleClass('opened');
      header.toggleClass('opened');
      header.find('.menu').toggleClass('opened');
    });
  },
  init__page__scroll: function() {},
});

//***************************************
//      Main program
//***************************************

$(document).ready(function(event) {
  Steenify.init();
});

$(window).on('scroll', function() {
  // Steenify.init__page__scroll();
});
