if (typeof window.Steenify === 'undefined') {
  window.Steenify = {};
}
window.Steenify = $.extend(Steenify, {
  init__home: function() {
    Steenify.init_banner__home();
    Steenify.init__toggle__interesting();
    Steenify.init__togglefade__div();
  },
  init_banner__home: function() {
    const banner = $('.banner');
    if (banner.length) {
      banner.slick({
        dots: true,
        nextArrow:
          '<button type="button" class="slick-next"><svg class="icon" xmlns="http://www.w3.org/2000/svg" width="8" height="15" viewBox="0 0 8 15"><g><g><path fill="#fff" d="M.85 14.169a.483.483 0 0 1-.35.145.495.495 0 0 1-.35-.845l6.155-6.155L.15 1.159a.495.495 0 0 1 .7-.7l6.505 6.505a.495.495 0 0 1 0 .7z"/></g></g></svg></button>',
        prevArrow:
          '<button type="button" class="slick-prev"><svg class="icon" xmlns="http://www.w3.org/2000/svg" width="8" height="15" viewBox="0 0 8 15"><g><g><path fill="#fff" d="M6.65 14.169a.483.483 0 0 0 .35.145.495.495 0 0 0 .35-.845L1.195 7.314 7.35 1.159a.495.495 0 0 0-.7-.7L.145 6.964a.495.495 0 0 0 0 .7z"/></g></g></svg></button>',
        autoplay: true,
        autoplaySpeed: 3000,
      });
    }
  },
  init__toggle__interesting: function() {
    $('.account .account__btn').on('click', function() {
      const self = $(this);
      const id = self.data('id');

      self.toggleClass('opened');

      $('#' + id)
        .stop()
        .slideToggle();
    });
  },
  init__togglefade__div: function() {
    $('.togglefade__control').on('click', function(e) {
      e.preventDefault();
      const self = $(this);
      const allInteresting = self
        .closest('.togglefade')
        .find('.togglefade__content');

      self.toggleClass('opened');

      allInteresting.stop().fadeToggle();
    });
  },
});
