if (typeof window.Steenify === 'undefined') {
  window.Steenify = {};
}
window.Steenify = $.extend(Steenify, {
  init__ed: function() {
    Steenify.init__toggle__comment__ed();
    Steenify.init__toggle__reply__ed();
  },
  init__toggle__comment__ed: function() {
    $('.comments__toggle').on('click', function() {
      const self = $(this);
      const content = self
        .closest('.comments')
        .find('.comments__toggle-content');

      self.toggleClass('opened');
      content.slideToggle();
    });
  },
  init__toggle__reply__ed: function() {
    $('.item__reply').on('click', function() {
      const self = $(this);
      const content = self.closest('.item').find('.item__form');

      self.toggleClass('opened');
      content.slideToggle();
    });
  },
});
